export default (layer) => {
  layer.highlight = true;
  layer.reload = () => {
    source.clear();
    source.refresh({force: true});
  };
  const source = new ol.source.Vector({
    loader: function(extent, resolution, projection) {
      if (layer.xhr)
        layer.xhr.abort();
      this.resolution = resolution;
      source.clear();
      const tableZ = layer.tableCurrent();
      if (!tableZ)
        return;
      layer.xhr = new XMLHttpRequest();
      layer.xhr.open("GET", layer.mapview.host + `/api/layer/cluster/${layer.mapview.getZoom()}?` + mapp.utils.paramString({
        locale: layer.mapview.locale.key,
        layer: layer.key,
        table: tableZ,
        kmeans: layer.cluster_kmeans,
        dbscan: layer.cluster_dbscan,
        pixelRatio: window.devicePixelRatio,
        aggregate: layer.style.theme && layer.style.theme.aggregate,
        theme: layer.style.theme && layer.style.themes && encodeURIComponent(Object.keys(layer.style.themes).find((k) => layer.style.themes[k] === layer.style.theme)),
        label: layer.style.label && layer.style.label.field,
        count: layer.style.label && layer.style.label.count,
        filter: layer.filter && layer.filter.current,
        viewport: [extent[0], extent[1], extent[2], extent[3]]
      }));
      layer.xhr.setRequestHeader("Content-Type", "application/json");
      layer.xhr.responseType = "json";
      layer.xhr.onload = (e) => {
        if (e.target.status !== 200)
          return;
        const cluster = e.target.response;
        layer.max_size = cluster.reduce((max_size, f) => Math.max(max_size, f.properties.size), 0);
        let id = 1;
        const features = cluster.map((f) => new ol.Feature({
          id: id++,
          geometry: new ol.geom.Point(layer.srid == 4326 && ol.proj.fromLonLat([f.geometry.x, f.geometry.y]) || [f.geometry.x, f.geometry.y]),
          properties: f.properties
        }));
        source.addFeatures(features);
      };
      layer.xhr.send();
    },
    strategy: function(extent, resolution) {
      this.resolution && this.resolution != resolution && this.loadedExtentsRtree_.clear();
      return [ol.proj.transformExtent(extent, "EPSG:" + layer.mapview.srid, "EPSG:" + layer.srid)];
    }
  });
  layer.L = new ol.layer.Vector({
    layer,
    source,
    zIndex: layer.style.zIndex || 10,
    style: Style
  });
  function Style(feature) {
    const properties = feature.getProperties().properties;
    const marker = properties.size > 1 ? Object.assign({}, layer.style.default, layer.style.cluster) : Object.assign({}, layer.style.default);
    marker.scale = marker.scale || 1;
    const theme = Object.assign({}, layer.style.theme);
    if (theme && theme.type === "categorized") {
      Object.assign(marker, theme.cat[properties.cat] && theme.cat[properties.cat].style || theme.cat[properties.cat]);
    }
    if (theme && theme.type === "graduated") {
      const value = parseFloat(properties.cat);
      if (value || value === 0) {
        for (let i = 0; i < theme.cat_arr.length; i++) {
          if (value < theme.cat_arr[i].value)
            break;
          var cat_style = theme.cat_arr[i].style || theme.cat_arr[i];
        }
        Object.assign(marker, cat_style);
      }
    }
    if (theme && theme.type === "competition") {
      let size2 = properties.size;
      let cat_style2 = {layers: {}};
      Object.entries(properties.cat).sort((a, b) => a[1] - b[1]).forEach((comp) => {
        if (theme.cat[comp[0]]) {
          cat_style2.layers[size2 / properties.size] = theme.cat[comp[0]].style.fillColor;
        }
        size2 -= comp[1];
      });
      Object.assign({}, marker, cat_style2);
    }
    let size = layer.cluster_logscale ? properties.count === 1 ? marker.size || layer.style.size : (marker.size || layer.style.size) + (marker.size || layer.style.size) / Math.log(layer.max_size) * Math.log(properties.size) : properties.count === 1 ? marker.size || layer.style.size : (marker.size || layer.style.size) + (marker.size || layer.style.size) / layer.max_size * properties.size;
    if (marker.layers && Array.isArray(marker.layers)) {
      return marker.layers.map((l) => {
        return new ol.style.Style({
          zIndex: parseInt(layer.max_size - properties.size),
          image: mapp.utils.icon(Object.assign({}, l, {
            scale: size / (l.size || layer.style.size) * (l.scale || 1) * (l.relative ? layer.mapview.getZoom() / (layer.mapview.locale.maxZoom - layer.mapview.locale.minZoom) : 1)
          }))
        });
      });
    }
    if (layer.highlight === feature.get("id")) {
      let tmpHighlightStyle = mapp.utils.lodash.cloneDeep(layer.style.highlight);
      delete tmpHighlightStyle.scale;
      if (marker.scale && layer.style.highlight.scale) {
        marker.scale *= layer.style.highlight.scale;
      }
      Object.assign(marker, tmpHighlightStyle);
    }
    const image = mapp.utils.icon(Object.assign({}, marker, {
      scale: size / (marker.size || layer.style.size) * (marker.scale || 1) * (marker.relative ? layer.mapview.getZoom() / (layer.mapview.locale.maxZoom - layer.mapview.locale.minZoom) : 1)
    }));
    return new ol.style.Style({
      zIndex: layer.style.zIndex,
      image
    });
  }
};
